#app {
    text-align: center;
}
.app-wrapper {
    display:inline-block;
}
.app-center {
    display: inline-block;
}
.app-right {
    display: inline-block;
    position: absolute;
    margin-left: 30px;
}
.app-header {
    background-color: #a2a2a2;
    height: 120px;
    padding: 10px;
    color: #424242;
    margin-bottom: 20px;
}
.app-title {
    font-size: 1.5em;
    margin: 0;
}
.app-intro {
    font-size: large;
    padding-bottom: 10px;
}
#app input {
    width: 40px;
}
.app-logo {
    -webkit-animation: app-logo-spin infinite 20s linear;
            animation: app-logo-spin infinite 20s linear;
    height: 80px;
}
.timer {
    margin:15px 0 0 0;
}
ul.info {
    list-style: none;
}
.info li {
    padding: 5px 0
}
.reset button {
    padding-bottom: 30px;
    font-size: 16px;
}
@-webkit-keyframes app-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
@keyframes app-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



